#app {
  text-align: center;
  margin: 100px;
}

html, body {
  padding: 0;
  margin: 0;
  height: 85vh;
}

#container {
  border: 1px solid black;
  border-radius: 10px;
}

.timerObject {
  border: 1px solid black;
  border-radius: 5px;
  padding: 0 15px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}